<template>
  <div class="footer-nav">
    <hr class="footer-nav-hr">
    <span
      v-if="leftNav"
      class="footer-nav-link footer-nav-left"
      @click="handleNavClick('prev')">
      <i class="ox-icon-back"></i>
      {{ leftNav.title || leftNav.desc }}
    </span>
    <span
      v-if="rightNav"
      class="footer-nav-link footer-nav-right"
      @click="handleNavClick('next')">
      {{ rightNav.title || rightNav.desc }}
      <i class="ox-icon-outline_go"></i>
    </span>
  </div>
</template>

<style lang="scss">
  .footer-nav-hr{
    margin:0px 0px 18px 0px;
    border: 0.5px solid #E7ECEF;
  }
  .footer-nav {
    // width: calc(100% - 305px);
    padding: 40px 0;
    color: #006BCC;
    font-size: 14px; 
  }
  .footer-nav i {
      transition: .3s;
      color: #101F3C;
      vertical-align: baseline;
    }
  .footer-nav::after {
      content: '';
      display: block;
      clear: both;
    }
  .footer-nav-link {
    cursor: pointer;
    transition: .3s;
  }
  .footer-nav-link :hover{
    color: #409EFF;
  }
  .footer-nav-link :hover i{
    color: #409EFF;
  }
  .footer-nav-left {
    float: left;
    margin-left: -4px;
  }
  
  .footer-nav-right {
    float: right;
    margin-right: -4px;
  }
</style>

<script>
import navConfig from '../nav.config.json'

export default {
  data () {
    return {
      currentComponent: null,
      nav: [],
      currentIndex: -1,
      leftNav: null,
      rightNav: null
    }
  },

  computed: {
    lang () {
      return this.$route.meta.lang
    }
  },

  watch: {
    '$route.path' () {
      this.setNav()
      this.updateNav()
    }
  },

  methods: {
    setNav () {
      let nav = navConfig
      for (var i in nav) {
        this.nav = this.nav.concat(nav[i])
      }
      // this.nav = [nav[0]].concat(nav[3].children)
      // nav[4].groups.map(group => group.list).forEach(list => {
      //   this.nav = this.nav.concat(list)
      // })
    },

    updateNav () {
      this.currentComponent = '/' + this.$route.path.split('/')[this.$route.path.split('/').length - 1]
      for (let i = 0, len = this.nav.length; i < len; i++) {
        if (this.nav[i].path.indexOf(this.currentComponent) !== -1) {
          this.currentIndex = i
          break
        }
      }
      this.leftNav = this.nav[this.currentIndex - 1]
      this.rightNav = this.nav[this.currentIndex + 1]
      if (!this.leftNav.desc) { this.leftNav = null }
      if (!this.rightNav.desc) { this.rightNav = null }
    },

    handleNavClick (direction) {
      this.$router.push(`${direction === 'prev' ? this.leftNav.path : this.rightNav.path}`)
      this.scrollParent = document.querySelector('.main-cnt__scroll > .ox-scrollbar__wrap')
      this.scrollParent && this.scrollParent.scrollTo(0, 0)
    }
  },

  created () {
    this.setNav()
    this.updateNav()
  }
}
</script>


