<style lang="scss" type="text/scss">
  @import "../assets/scss/index";
  .page-header {
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    transition: all .3s;
    z-index: 100;
    .header-container{
      // width: 1150px;
      width:85%;
      margin: 0 auto;
      display: flex;
      align-items: center;
      height: 80px;
      justify-content: space-between;
      .component{
        color: #101F3C;
        // line-height: 16px;
        display:block;
        font-size:16px;
        padding:32px 0px 28px 0px;
        position:relative;
      }
      .component:hover{
      font-weight:bold;
        color: #101F3C;
      }
      .component:hover:after{
        // line-height: 17px;
        // display:block;
        // border-bottom:4px solid #3484F5;
        // padding:32px 0px 24px 0px;
        position:absolute;
        content:"";
        width:20px;
        height:1px;
        left:5px;
        bottom:0px;
        border-bottom:4px solid #006BCC;
        
      }
    }
  }
</style>
<template>
  <header class="page-header" :style="'box-shadow:0 6px 12px 0 rgba(218,221,229,0.13);'">
    <div class="header-container">
      <router-link :to="{name: 'index'}"><img src="../assets/img/header_logo.png" width="157"></router-link>
      <router-link :to="{name: 'giud'}"><span class="component">组件</span></router-link>
    </div>
  </header>
</template>

<script>
  export default {
    data () {
      return {}
    }
  }
</script>



