<template>
  <div id="app" :class="{'is-component':!isIndex}">  
    <mainHeader></mainHeader> 
    <div class="main-cnt"   v-if="!isIndex">
      <ox-scrollbar class="main-cnt__scroll">
        <div class="page-container page-component">
          <ox-scrollbar class="page-component__nav">
            <sideNav class="nav"></sideNav>
          </ox-scrollbar>
          <div class="page-component__content">
         <router-view class="content"></router-view>
          <footer-nav></footer-nav>
          </div> 
        </div>
      </ox-scrollbar>
    </div>
    <div v-else>
        <router-view class="page"></router-view>
        <mainFooter></mainFooter>
    </div> 
  </div>
</template>

<script>
  import mainHeader from './components/header.vue'
  import mainFooter from './components/footer.vue'
  import sideNav from './components/side-nav.vue'
  import footerNav from './components/footer-nav'

  export default {
    name: 'app',
    data () {
      return {
        isIndex: true
      }
    },
    watch: {
      $route () {
        this.isIndex = this.$route.name === 'index'
        !this.isIndex && document.body.classList.add('is-component')
        this.isIndex && document.body.classList.remove('is-component')
      }
    },
    mounted () {
      if (!this.isIndex) {
        document.body.classList.add('is-component')
      }
    },
    destroyed () {
      document.body.classList.remove('is-component')
    },
    components: {
      mainHeader,
      sideNav,
      mainFooter,
      footerNav
    }
  }
</script>

<style lang="scss"  type="text/scss">
  @import "./assets/scss/index";
  body,html{
    background-color: #fff;
    height: 100%;
    overflow-x: hidden;
  }
  blockquote {
    margin:25px !important;
  }
  body.is-component, html.is-component{
    overflow: hidden;
  }
  #app{
    height: 100%;
    &.is-component {
    overflow-y: hidden;
    }
  }
  .main-cnt{
    padding: 0;
    margin-top: 0;
    height: 100%;
    min-height: auto;
    .main-cnt__scroll{
        margin-top: 80px;
        height: calc(100% - 80px);
    }
  }
  .page-container.page-component {
    // margin: 40px auto;
    margin:0 auto;
    width: 85%;
    background-color: #fff;
    height: 100%;
    .page-component__nav{
      width: 300px;
      margin-top:80px;
      position: fixed;
      top: 0px;
      bottom: 0;
      transition: padding-top .3s;
      height: calc(100% - 80px);
    }
    .nav {
      float: left;
      width: 300px;
        // width: 210px;
    }
    .content {
      box-sizing: border-box;
      width: 100%;
    }
  }
  
  .page-container.page-component:after {
    content: "";
    clear: both;
    display: block;
  }

    .page-component {
    box-sizing: border-box;
    height: 100%;
  
    &.page-container {
      padding: 0;
    }

    .page-component__nav {
      width: 240px;
      position: fixed;
      top: 0;
      bottom: 0;
      margin-top: 80px;
      transition: padding-top .3s;

      .el-scrollbar__wrap {
        height: 100%;
      }

      &.is-extended {
        padding-top: 0;
      }
    }

    .side-nav {
      // height: 100%;
      // padding-top: 50px;
      // padding-bottom: 50px;
      // padding-right: 0;

      & > ul {
        padding-bottom: 50px;
      }
    }

    .page-component__content {
      padding-left: 340px;
      padding-bottom: 100px;
      box-sizing: border-box;
    }

    .content {
      padding-top: 50px;

      > {
        h3 {
          margin: 55px 0 20px;
        }

        table {
          border-collapse: collapse;
          width: 100%;
          background-color: #fff;
          font-size: 14px;
          margin-bottom: 45px;
          line-height: 1.5em;

          strong {
            font-weight: normal;
          }

          td, th {
            border-bottom: 1px solid #d8d8d8;
            padding: 15px;
            max-width: 250px;
          }

          th {
            text-align: left;
            white-space: nowrap;
            color: #666;
            font-weight: normal;
          }

          td {
            color: #333;
          }

          th:first-child, td:first-child {
            padding-left: 10px;
          }
        }

        ul:not(.timeline) {
          margin: 10px 0;
          padding: 0 0 0 20px;
          font-size: 14px;
          color: #5e6d82;
          line-height: 2em;
        }
      }
    }

    .page-component-up {
      background-color: #fff;
      position: fixed;
      right: 100px;
      bottom: 150px;
      size: 40px;
      border-radius: 20px;
      cursor: pointer;
      transition: .3s;
      box-shadow: 0 0 6px rgba(0,0,0, .12);
      z-index: 5;

      i {
        color: #409EFF;
        display: block;
        line-height: 40px;
        text-align: center;
        font-size: 18px;
      }

      &.hover {
        opacity: 1;
      }
    }
    .back-top-fade-enter,
    .back-top-fade-leave-active {
      transform: translateY(-30px);
      opacity: 0;
    }
  }
    .hljs.language-bash,
  .hljs.language-js{
    line-height: 1.8;
    font-family: Menlo,Monaco,Consolas,Courier,monospace;
    font-size: 12px;
    padding: 18px 24px;
    background-color: #fafafa;
    border: 1px solid #eaeefb;
    margin-bottom: 25px;
    border-radius: 4px;
    -webkit-font-smoothing: auto;
  }
</style>



