<template>
  <div class="side-nav">
    <div v-for="title in (Object.keys(data))" class="group-container">
      <p class="side-nav-title">{{title}}</p>
      <div class="side-nav-items" v-for="nav in data[title]" v-if="nav.desc">
        <router-link :class="$route.name===nav.name ? 'active' : ''" v-if="nav.name" :to="{name: nav.name}">{{nav.desc}}</router-link>
        <p v-else class="side-nav-group">{{nav.desc}}</p>
        <!-- <div v-for="item in nav.items">
          <router-link :to="{name: item.name}" :class="$route.name===item.name ? 'active' : ''" class="slid-nav-component">{{item.desc}}</router-link>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
  import navConf from '../nav.config.json'
  export default {
    data () {
      return {
        data: navConf
      }
    }
  }
</script>
<style lang="scss" type="text/scss">
  .side-nav{
    display: inline-block;
    // margin: 32px 0;
    margin:39px 0px 20px 0px;
    padding: 0;
    color: #3F536E;
    background-color: #fff;
    z-index: 99;
    .group-container{
      margin-bottom: 32px;
    }
    .side-nav-title{
      // padding-top:9px;
      // padding-left:60px;
      // padding-bottom:11px;
      padding: 10px 60px 10px;
      color: #202020;
      font-size: 14px;
      //font-weight: bold;
      letter-spacing: 0px;
      //text-transform: uppercase;
      line-height: 20px;
      font-weight:bold; 
    }
    .side-nav-items{
      font-size: 14px;
      font-weight: normal;
      line-height: 1.8;
      a{
        display: block;
        position: relative;
         padding: 10px 0px 10px 60px;
        // font-weight: normal;
         cursor: pointer;

        font-size: 14px;
        color: #101F3C;
        letter-spacing: 0;
        line-height: 20px;
        &:hover{
          background: #ECF6FF;
          border-left:4px solid #006BCC;
          padding: 10px 0px 10px 56px;
        }
      }
      .side-nav-group{
        display: block;
        position: relative;
        padding: 6px 0 6px 24px;
        color: #2C405A;
        font-weight: bold;
      }
      .slid-nav-component{
        display: block;
        position: relative;
        padding: 0px 24px 0px 32px;
        color: #616367;
        font-size: 14px;
        line-height: 40px;
      }
      .active{
        color: #006BCC;
      }
    }
  }
</style>


